export default {
	props: {
			item:Object,
			index:Number,
			choose:{
				type:Boolean,
				default:false
			}
		},
		methods: {
			open() {
				if(this.choose){
					uni.$emit('chooseTopic',{
						id:this.item.id,
						title:this.item.title
					})
					return uni.navigateBack({
						delta:1
					})
				}
				uni.navigateTo({
					url:'../../pages/topic-detail/topic-detail?detail='+JSON.stringify(this.item)
				})
			}
		},
}
